import React, { useEffect, useState } from "react";
import axios from 'axios';
import { SERVER_API, SERVER_ASSET } from "config";
import { toast } from 'react-toastify';
import { useParams } from 'react-router-dom';
import { useDispatch } from "react-redux";
import { addItem } from 'store/cart';

import SlideShow from 'UI/SlideShow';
import ProductsList from "components/ProductsList";

import './style.scss';
export default function DetailPage() {
    const dispatch = useDispatch();
    const client = axios.create({ baseURL: SERVER_API });

    const { id } = useParams();

    const [quantity, setQuantity] = useState(1);
    const [product, setProduct] = useState(null);
    const [related, setRelated] = useState([null, null, null, null]);
    const [onload, setOnload] = useState(true);

    const catchError = e => {
        if (e.response && e.response.data) {
            toast.error(e.response.data.errors[0]);
        } else {
            toast.error('Kết nối máy chủ thất bại, vui lòng thử lại');
        }
    }

    const getData = () => {
        client.get('/product?id=' + id).then((rs) => {
            setProduct(s => rs.data.data[0]);
            client.get('/products?category=' + rs.data.data[0].category).then((rs) => {
                setOnload(false);
                setRelated(s => rs.data.data);
            }).catch(e => catchError(e));
        }).catch(e => catchError(e));
    }
    useEffect(() => { getData(); }, [id]);

    const addToCart = () => {
        dispatch(addItem({
            id: product._id,
            img: SERVER_ASSET + product.images[0],
            name: product.name,
            price: product.price,
            amount: quantity
        }));
        toast.success('Đã thêm ' + product.name + ' vào giỏ hàng!')
    }

    const descQuantity = () => {
        if (quantity > 1) { setQuantity(s => s - 1); }
    }

    const incQuantity = () => {
        setQuantity(s => s + 1);
    }

    return (
        <main>
            <div className="container" style={{ backgroundColor: '#fff' }}>
                <div className="gap--50"></div>
                <div className="flex flex--gap">
                    <div className="col-sm-12 col-lg-6">
                        {product && product.images && (
                            <SlideShow items={product.images.map(i => SERVER_ASSET + i)} />
                        )}
                    </div>
                    <div className="col-sm-12 col-lg-6">
                        {product && (
                            <div className="detailpage-detail">
                                <h2>{product.name}</h2>
                                <div className="gap--10"></div>
                                <h4>{new Intl.NumberFormat("de-DE").format(product.price) + ' VNĐ'}</h4>
                                <div className="gap--20"></div>
                                <div className="break-word">{product.title}</div>
                                <div className='gap--20'></div>
                                <div>
                                    <h5>Danh mục: {product.category}</h5>
                                </div>
                                <div className="gap--20"></div>
                                <div className="flex y--center">
                                    <div className="quantity">
                                        <span>Số lượng</span>
                                        <button onClick={descQuantity}>
                                            <i className="fa fa-caret-left"></i>
                                        </button>
                                        <span>{quantity}</span>
                                        <button onClick={incQuantity}>
                                            <i className="fa fa-caret-right"></i>
                                        </button>
                                    </div>
                                    <button className='btn black btn--icon pi--30' onClick={addToCart}>
                                        <i className='fa fa-shopping-cart'></i> Thêm vào giỏ hàng
                                    </button>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
                <div className="gap--20"></div>
                <div style={{ borderTop: '1px solid #ddd' }}></div>
                <div className="gap--20"></div>
                <h5 className="pi--10">Thông tin sản phẩm</h5>
                <div className="gap"></div>
                <div className="break-word pi--20">{product ? product.desc : ''}</div>
                <div className="gap--50"></div>
                <h5 className="pi--10">Sản phẩm tương tự</h5>
                <div className="gap"></div>
                <ProductsList items={related[0] !== null ? related.filter(x => x._id !== id) : related} column={4} onload={onload} />
                <div className="gap--50"></div>
            </div>
        </main>
    )
}