import { useState } from 'react';

export default function SlideShow(props) {
    const [previewImage, setPreviewImage] = useState(0);
    const setActiveImage = (i) => {
        setPreviewImage(i);
    }

    return (
        <div className='flex flex--gap slideshow'>
            <div className='col--min'>
                <ul className='slideshow__list'>
                    {props.items.length > 0 && (
                        props.items.map((url, i) => {
                            return <li key={i} onClick={() => setActiveImage(i)}><img src={url} /></li>
                        })
                    )}
                </ul>
            </div>
            <div className='col'>
                <div className='slideshow__preview'>
                    <img src={props.items[previewImage]} alt='' />
                </div>
            </div>
        </div>
    )
}